<template>
  <div class="wrapper">
    <div class="btn1" @click="onClickBtn1"></div>
    <div class="btn2" @click="isDream"></div>
    <div class="star1"></div>
    <div class="star2"></div>
    <div class="start"></div>
    <div class="man"></div>
    <div class="way"></div>
    <div class="supper_star"></div>
    <div class="bird1"></div>
    <div class="bird2"></div>
    <div class="bird3"></div>
    <div class="bird4"></div>
    <div class="mask" ref="mask">
      <div class="message"></div>
      <div class="dredgeBtn" @click="$router.push('/compile')"></div>
    </div>
    <div class="dream-box1" v-show="flag">
      <img
        v-for="(item, index) in dreamData1"
        :src="item.src"
        :key="index"
        :class="'img' + index"
        @click="goDetail(item.detailData)"
      />
    </div>
    <div class="dream-box2" v-show="!flag">
      <img
        v-for="(item, index) in dreamData2"
        :src="item.src"
        :key="index"
        :class="'img' + index"
        @click="goDetail(item.detailData)"
      />
    </div>
  </div>
</template>

<script>
import { dreamData1, dreamData2 } from "../data/dataSource";
import { Toast } from "vant";
export default {
  name: "Dream",
  components: {
    [Toast.name]: Toast,
  },
  data() {
    return {
      flag: true,
      timer: null,
      dreamData1,
      dreamData2,
    };
  },
  mounted() {
    this.timer = setInterval(() => {
      this.flag = !this.flag;
    }, 6000);
  },
  destroyed() {
    this.timer = null;
  },
  methods: {
    isDream() {
      let dream = localStorage.getItem("dream");
      if (!dream) {
        this.$refs.mask.style.display = "block";
      } else {
        localStorage.setItem("isShow", false);
        this.$router.push("/end");
      }
    },
    goDetail(item) {
      this.$router.push({
        path: "/celebrity",
        query: {
          item: JSON.stringify(item),
        },
      });
    },
    onClickBtn1() {
      let dream = localStorage.getItem("dream");
      if (dream) {
        Toast({
          duration: 1800,
          message: "您已存储过梦想，请查看梦想。",
        });
      } else {
        this.$router.push("/compile");
      }
    },
  },
};
</script>
